<template>
    <div class="cell-date-zone">
        <label>{{ title }}</label>
        <ul class="zone-list">
            <li class="item" v-for="( item, index ) in zoneList">
                <mt-button v-if=" item.id ===  checkedId " size="small" type="primary" @click="change( item.id )">{{ item.text }}</mt-button>
                <mt-button v-if=" item.id !==  checkedId " size="small" type="default" @click="change( item.id )">{{ item.text }}</mt-button>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: [
        'title'
    ],
    data() {
        return {
            resolve: '',
            reject: '',
            promise: '',
            checkedId: -1,
            zoneList: [
                {
                    id: 1,
                    text: ''
                },
                {
                    id: 2,
                    text: ''
                },
                {
                    id: 3,
                    text: ''
                }
            ]
        };
    },
    created() {
    },
    methods: {
        setDefault (begin, end) {
            var now = new Date().format('yyyy-MM-dd');
            if( now !== end ){
                this.change(1);
                return;
            }
            if( begin === this._getDateZoneWeek() ){
                this.checkedId = 1;
            }
            if( begin === this._getDateZoneMonth() ){
                this.checkedId = 2;
            }
            if( begin === this._getDateZone3Month() ){
                this.checkedId = 3;
            }
        },
        change ( id ) {
            this.checkedId = id;
            var begin = 0;
            var end = new Date().format('yyyy-MM-dd');

            if ( id === 1 ) {
                begin = this._getDateZoneWeek();
            } else if ( id === 2 ){
                begin = this._getDateZoneMonth();
            } else if ( id === 3 ){
                begin = this._getDateZone3Month();
            }
            console.log('begin' + new Date(begin).format('yyyy-MM-dd') + ' -  end' + new Date(end).format('yyyy-MM-dd') );
            this.$emit('date-zone-change', begin, end );
        },
        _getDateZoneWeek (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 7 ) ).format('yyyy-MM-dd');
        },
        _getDateZoneMonth (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 30 ) ).format('yyyy-MM-dd');
        },
        _getDateZone3Month (){
            var now = new Date();
            var month = now.getMonth();
            return new Date( now.setMonth( month - 2 ) ).format('yyyy-MM-dd');
        }
    }
};

</script>

<style lang="scss" scoped>

    .cell-date-zone
    {
        position: relative;
        padding: 20px 15px;
        background-color: #fff;

        @include mini-border(bottom, #d9d9d9, after);
        @include mini-border(top, #d9d9d9, before);

        label
        {
            font-size: 14px;
        }
    }

    .zone-list
    {
        text-align: right;
        .item
        {
            display: inline-block;
            margin-left: 40px;
            text-align: center;

            .mint-button
            {
                padding: 0px 24px;
                height: 48px;
                font-size: 12px;
            }
        }
        .item:first-child
        {
            text-align: left;
        }
    }
</style>


